import { AlertState } from '@bi/volkovlabs-engine-datasource';

import { TagName } from './Tag';

/**
 * Get Tag Name By Alert State
 * @param state
 */
export const getTagNameByAlertState = (state: AlertState): TagName | undefined => {
  switch (state) {
    case AlertState.OK: {
      return TagName.STATE_OK;
    }
    case AlertState.SCHEDULED: {
      return TagName.STATE_SCHEDULED;
    }
    case AlertState.ALERTING: {
      return TagName.STATE_ALERTING;
    }
    case AlertState.ERROR: {
      return TagName.STATE_ERROR;
    }
    default: {
      return;
    }
  }
};
