import { notifySuccess } from '@bi/volkovlabs-engine-datasource';
import { Button, useStyles2 } from '@grafana/ui';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { TEST_IDS } from '../../constants';
import { getStyles } from './AlertEventDetails.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Value
   *
   * @type {string}
   */
  value: string;
}

/**
 * Alert Event Details
 * @constructor
 */
export const AlertEventDetails: React.FC<Props> = ({ value }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * On Copy
   */
  const onCopy = useCallback(() => {
    navigator.clipboard.writeText(value);
    notifySuccess([
      t('alertingPanel.panel.alertOverview.eventDetailsCopiedTitle'),
      t('alertingPanel.panel.alertOverview.eventDetailsCopiedMessage'),
    ]);
  }, [t, value]);

  return (
    <>
      {!!value && (
        <Button
          icon="copy"
          className={styles.button}
          onClick={onCopy}
          data-testid={TEST_IDS.alertEventDetails.buttonCopy}
        >
          {t('alertingPanel.panel.alertOverview.copyEventDetailsButton')}
        </Button>
      )}
      <code className={styles.code} data-testid={TEST_IDS.alertEventDetails.info}>
        {value || t('alertingPanel.panel.alertOverview.noEventDetailsMessage')}
      </code>
    </>
  );
};
