import { FORMATS } from '@bi/i18n';
import { DataSourceApi, useData } from '@bi/volkovlabs-engine-datasource';
import { Alert, FieldSet, LoadingPlaceholder, useStyles2 } from '@grafana/ui';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { TEST_IDS } from '../../constants';
import { getStyles } from './Configuration.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;
}

/**
 * Configuration
 * @constructor
 */
export const Configuration: React.FC<Props> = ({ datasource }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Load license info
   */
  const {
    data: licenseInfo,
    loading,
    error,
  } = useData({
    initial: null,
    query: useCallback((datasource) => {
      return datasource.api.license.get();
    }, []),
    datasource,
  });

  if (loading) {
    return (
      <div data-testid={TEST_IDS.configuration.loadingMessage}>
        <Alert severity="info" title={t('basic.wait')}>
          <LoadingPlaceholder text={t('messages.loading')} />
        </Alert>
      </div>
    );
  }

  /**
   * Error
   */
  if (error) {
    return (
      <div data-testid={TEST_IDS.configuration.errorMessage}>
        <Alert severity="error" title={t('messages.notAvailableTitle')}>
          {t('messages.notAvailableMessage')}
        </Alert>
      </div>
    );
  }

  return (
    <div data-testid={TEST_IDS.configuration.root}>
      {licenseInfo && (
        <>
          <FieldSet label={t('alertingPanel.panel.configuration.license.label')}>
            <div className={styles.limits}>
              <div className={styles.limitItem}>
                <div className={styles.limitLabel}>{t('alertingPanel.panel.configuration.license.usageLabel')}</div>
                <h2 className={styles.limitContent} data-testid={TEST_IDS.configuration.usageInfo}>
                  {licenseInfo.used}
                  <span className={styles.limitValue}> / {licenseInfo.limit}</span>
                </h2>
              </div>
              <div className={styles.limitItem}>
                <div className={styles.limitLabel}>
                  {t('alertingPanel.panel.configuration.license.expirationLabel')}
                </div>
                <h2 className={styles.limitContent} data-testid={TEST_IDS.configuration.expirationInfo}>
                  {t('alertingPanel.panel.configuration.license.expirationDate', {
                    date: new Date(licenseInfo.expiration),
                    formatParams: {
                      date: FORMATS.date,
                    },
                  })}
                </h2>
              </div>
            </div>
          </FieldSet>
        </>
      )}
    </div>
  );
};
