import { useCallback, useEffect, useState } from 'react';

import { DataSourceApi } from '../types';

/**
 * Data Source With Api
 */
type DatasourceWithApi = DataSourceApi & { api: Required<DataSourceApi>['api'] };

/**
 * Type for useCallback function
 */
type UseCallback<T extends (datasource: DatasourceWithApi) => ReturnType<T>> = ((
  ...args: Parameters<T>
) => ReturnType<T>) & {
  /**
   * Specified property to check if wrapped in useCallback
   */
  // eslint-disable-next-line @typescript-eslint/naming-convention
  __IS_USE_CALLBACK?: undefined;
};

/**
 * Use Data
 */
export const useData = <T>({
  datasource,
  query,
  initial,
}: {
  datasource: DataSourceApi | null;
  query: UseCallback<(datasource: DatasourceWithApi) => Promise<T | undefined>>;
  initial: T;
}) => {
  const [data, setData] = useState<T>(initial);
  const [loading, setLoading] = useState(false);
  const [loaded, setLoaded] = useState(false);
  const [error, setError] = useState(false);

  /**
   * Load
   */
  const load = useCallback(async () => {
    if (!datasource || !datasource.api) {
      setError(true);
      setLoaded(false);
      return;
    }

    /**
     * Start Loading
     */
    setLoading(true);
    setError(false);
    setLoaded(false);

    try {
      const data = await query({
        ...datasource,
        api: datasource.api,
      });

      if (data !== undefined) {
        setData(data);
        setLoading(false);
        setLoaded(true);
        setError(false);
      }
    } catch {
      setLoading(false);
      setError(true);
      setLoaded(false);
    }
  }, [datasource, query]);

  /**
   * Load Data
   */
  useEffect(() => {
    load();
  }, [datasource, load, query]);

  return {
    data,
    loading,
    update: load,
    error,
    loaded,
  };
};
