export const ACTION_MESSAGE_SUGGESTIONS = (t: TFunction): CodeEditorSuggestionItem[] =>
  new CodeParametersBuilder(
    {
      detail: t('actionMessageSuggestions.event.label'),
      items: {
        alert: {
          detail: t('actionMessageSuggestions.event.alert.label'),
          items: {
            title: new CodeParameterItem<string>(t('actionMessageSuggestions.event.alert.title.label')),
            state: new CodeParameterItem<AlertState>(t('actionMessageSuggestions.event.alert.state.label')),
          },
        },
        error: new CodeParameterItem<boolean>(t('actionMessageSuggestions.event.error.label')),
        errorDetails: new CodeParameterItem<unknown>(t('actionMessageSuggestions.event.errorDetails.label')),
      },
    },
    'event'
  ).suggestions;
