import { useDatasourceApi } from '@bi/volkovlabs-engine-datasource';
import { css, cx } from '@emotion/css';
import { PanelProps } from '@grafana/data';
import { LoadingBar, RadioButtonGroup, useStyles2 } from '@grafana/ui';
import React, { useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { TAB_OPTIONS, TEST_IDS } from '../../constants';
import { usePermissions } from '../../hooks';
import { PanelOptions, TabName } from '../../types';
import { getOptionsWithTestId } from '../../utils';
import { ActionsList } from '../ActionsList';
import { AlertsList } from '../AlertsList';
import { Configuration } from '../Configuration';
import { getStyles } from './AlertingPanel.styles';

/**
 * Properties
 */
interface Props extends PanelProps<PanelOptions> {}

/**
 * Panel
 */
export const AlertingPanel: React.FC<Props> = ({ data, width, height, eventBus }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Permissions
   */
  const permissions = usePermissions();

  /**
   * Data Source Api
   */
  const datasource = useDatasourceApi(data);

  /**
   * States
   */
  const [activeTab, setActiveTab] = useState(TabName.ALERTS);
  const [loading, setLoading] = useState(false);

  /**
   * Tab Options
   */
  const tabOptions = useMemo(() => {
    const options = getOptionsWithTestId(TAB_OPTIONS(t), TEST_IDS.panel.tabOption);

    return options.filter((option) => {
      if (option.value === TabName.CONFIGURATION) {
        return permissions.configuration.view;
      }
      return true;
    });
  }, [permissions.configuration.view, t]);

  /**
   * Return
   */
  return (
    <div
      data-testid={TEST_IDS.panel.root}
      className={cx(
        styles.wrapper,
        css`
          width: ${width}px;
          height: ${height}px;
        `
      )}
    >
      {loading && (
        <div className={styles.loadingBar} data-testid={TEST_IDS.panel.loadingBar}>
          <LoadingBar width={100} />
        </div>
      )}

      <div className="page-action-bar">
        <RadioButtonGroup value={activeTab} options={tabOptions} onChange={setActiveTab} />
      </div>

      {activeTab === TabName.ALERTS && (
        <AlertsList datasource={datasource} eventBus={eventBus} setLoading={setLoading} />
      )}
      {activeTab === TabName.ACTIONS && (
        <ActionsList datasource={datasource} eventBus={eventBus} setLoading={setLoading} />
      )}
      {activeTab === TabName.CONFIGURATION && permissions.configuration.view && (
        <Configuration datasource={datasource} />
      )}
    </div>
  );
};
