import { OrgRole } from '@grafana/data';
import { config } from '@grafana/runtime';
import { useMemo } from 'react';

/**
 * Use Permissions
 */
export const usePermissions = () => {
  /**
   * Is Editor
   */
  const isEditor = config.bootData.user.orgRole === OrgRole.Editor || config.bootData.user.orgRole === OrgRole.Admin;

  return useMemo(
    () => ({
      alerts: {
        add: isEditor,
        view: true,
        viewEventDetails: isEditor,
        edit: isEditor,
        changeStatus: true,
        delete: isEditor,
      },
      actions: {
        add: isEditor,
        view: true,
        edit: isEditor,
        test: true,
        delete: isEditor,
      },
      configuration: {
        view: isEditor,
      },
    }),
    [isEditor]
  );
};
