import { ALL_VARIABLE_TEXT, ALL_VARIABLE_VALUE } from '@bi/constants';
import { AlertVariable, AlertVariableOption } from '@bi/volkovlabs-engine-datasource';

/**
 * Is Multi Value
 * @param variable
 * @param repeat
 */
export const isMultiValue = (variable: AlertVariable, repeat: string | null): boolean => {
  return variable.multi || repeat === variable.name;
};

/**
 * Add All Option
 */
export const addAllOption = (options: AlertVariableOption[]): AlertVariableOption[] => {
  /**
   * All Value Already Exists
   */
  if (options.some((item) => item.value === ALL_VARIABLE_VALUE)) {
    return options;
  }

  /**
   * Add All Option
   */
  return [
    {
      value: ALL_VARIABLE_VALUE,
      label: ALL_VARIABLE_TEXT,
    },
    ...options,
  ];
};

/**
 * Remove all option
 * @param options
 */
export const removeAllOption = (options: AlertVariableOption[]): AlertVariableOption[] => {
  return options.filter((option) => option.value !== ALL_VARIABLE_VALUE);
};

/**
 * Is Value Exist
 */
export const isValueExist = (value: number | string | null | undefined): value is number | string => {
  return value !== undefined && value !== null;
};
