import { getOptionsWithTestId } from '@bi/utils';
import { Action, ActionMethod, ActionType, DataSourceApi } from '@bi/volkovlabs-engine-datasource';
import { Alert, Button, FieldSet, InlineField, Input, RadioButtonGroup, useStyles2 } from '@grafana/ui';
import { AutosizeCodeEditor } from '@volkovlabs/components';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { ACTION_MESSAGE_SUGGESTIONS, LABEL_WIDTH, TEST_IDS } from '../../constants';
import { ActionHeadersEditor } from '../ActionHeadersEditor';
import { getStyles } from './ActionDetails.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * On Close
   */
  onClose: () => void;

  /**
   * On Save
   */
  onSave: (data: Action) => void;

  /**
   * Initial
   */
  initial?: Action;
}

/**
 * Action Details
 */
export const ActionDetails: React.FC<Props> = ({ datasource, onSave, onClose, initial }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Alert
   */
  const [action, setAction] = useState<Action>(
    initial || {
      id: '',
      title: '',
      type: ActionType.REQUEST,
      options: {
        url: '',
        headers: [],
      },
      isDefault: false,
      message: '{{json event}}',
    }
  );

  /**
   * Testing Results
   */
  const [isTesting, setTesting] = useState(false);
  const [testResult, setTestResult] = useState<{ ok: boolean; message: string } | null>(null);

  /**
   * Saving
   */
  const [saving, setSaving] = useState(false);

  /**
   * On Test
   */
  const onTest = useCallback(async () => {
    if (!datasource || !datasource.api) {
      return;
    }

    /**
     * Reset testing state
     */
    setTesting(true);
    setTestResult(null);

    const { useFor, useForByDefault, ...actionForTesting } = action;

    /**
     * Test Result
     */
    const data = await datasource.api.actions.test(actionForTesting);

    /**
     * Save Test Results
     */
    setTestResult(data);
    setTesting(false);
  }, [datasource, action]);

  /**
   * Type Options
   */
  const typeOptions = useMemo(() => {
    return getOptionsWithTestId(
      [
        {
          value: ActionType.REQUEST,
          label: t('alertingPanel.panel.actionType.request'),
          icon: 'cloud-upload',
        },
        {
          value: ActionType.CUSTOM,
          label: t('alertingPanel.panel.actionType.custom'),
          icon: 'bolt',
        },
      ],
      TEST_IDS.actionDetails.typeOption
    );
  }, [t]);

  /**
   * Is Default Options
   */
  const isDefaultOptions = useMemo(() => {
    return getOptionsWithTestId(
      [
        {
          value: true,
          label: t('alertingPanel.panel.actionDetails.isDefaultOptions.enabled'),
          icon: 'check-square',
        },
        {
          value: false,
          label: t('alertingPanel.panel.actionDetails.isDefaultOptions.disabled'),
          icon: 'square-shape',
        },
      ],
      TEST_IDS.actionDetails.defaultOption
    );
  }, [t]);

  /**
   * Method Options
   */
  const methodOptions = useMemo(
    () => [
      {
        value: ActionMethod.POST,
        label: ActionMethod.POST,
      },
    ],
    []
  );

  /**
   * Is Invalid
   */
  const isInvalid = useMemo(() => {
    const isBaseInvalid = !action.title || !action.type;

    if (isBaseInvalid) {
      return true;
    }

    if (action.type === ActionType.REQUEST) {
      /**
       * No URL
       */
      if (!action.options.url) {
        return true;
      }

      /**
       * Invalid Headers
       */
      if (action.options.headers.some((item) => item.key.trim().length === 0)) {
        return true;
      }
    }

    return false;
  }, [action]);

  return (
    <>
      <FieldSet>
        <InlineField
          label={t('alertingPanel.panel.actionDetails.titleLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <Input
            onChange={(event) => {
              setAction({
                ...action,
                title: event.currentTarget.value,
              });
            }}
            value={action.title}
            data-testid={TEST_IDS.actionDetails.fieldTitle}
          />
        </InlineField>
        <InlineField
          label={t('alertingPanel.panel.actionDetails.typeLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
          data-testid={TEST_IDS.actionDetails.fieldType}
        >
          <RadioButtonGroup
            onChange={(type) => {
              setAction((action) => {
                if (type === ActionType.REQUEST) {
                  return {
                    ...action,
                    type,
                    options: {
                      url: '',
                      headers: [],
                    },
                  };
                }

                if (type === ActionType.CUSTOM) {
                  return {
                    ...action,
                    type,
                    options: {},
                  };
                }

                return action;
              });
            }}
            value={action.type}
            options={typeOptions}
          />
        </InlineField>
        <InlineField
          label={t('alertingPanel.panel.actionDetails.defaultLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          disabled={initial?.isDefault && !!initial?.useForByDefault?.length}
          data-testid={TEST_IDS.actionDetails.fieldDefault}
        >
          <RadioButtonGroup
            onChange={(isDefault) => {
              setAction({
                ...action,
                isDefault,
              });
            }}
            value={action.isDefault}
            options={isDefaultOptions}
          />
        </InlineField>
      </FieldSet>

      {action.type === ActionType.REQUEST && (
        <FieldSet
          label={t('alertingPanel.panel.actionDetails.requestOptions.label')}
          data-testid={TEST_IDS.actionDetails.requestOptions}
        >
          <InlineField
            label={t('alertingPanel.panel.actionDetails.requestOptions.urlLabel')}
            grow={true}
            labelWidth={LABEL_WIDTH}
            required={true}
          >
            <Input
              onChange={(event) => {
                setAction({
                  ...action,
                  options: {
                    ...action.options,
                    url: event.currentTarget.value,
                  },
                } as typeof action);
              }}
              value={action.options.url}
              data-testid={TEST_IDS.actionDetails.fieldRequestUrl}
            />
          </InlineField>
          <InlineField
            label={t('alertingPanel.panel.actionDetails.requestOptions.methodLabel')}
            grow={true}
            labelWidth={LABEL_WIDTH}
            data-testid={TEST_IDS.actionDetails.fieldRequestMethod}
          >
            <RadioButtonGroup value={ActionMethod.POST} options={methodOptions} />
          </InlineField>
          <ActionHeadersEditor
            value={action.options.headers}
            onChange={(headers) => {
              setAction({
                ...action,
                options: {
                  ...action.options,
                  headers,
                },
              });
            }}
          />
        </FieldSet>
      )}
      <FieldSet label={t('alertingPanel.panel.actionDetails.message.label')}>
        <AutosizeCodeEditor
          value={action.message}
          onBlur={(message) =>
            setAction({
              ...action,
              message,
            })
          }
          language="handlebars"
          data-testid={TEST_IDS.actionDetails.fieldMessage}
          getSuggestions={() => ACTION_MESSAGE_SUGGESTIONS(t)}
        />
      </FieldSet>
      {!!testResult && (
        <>
          {testResult.ok ? (
            <Alert
              severity="success"
              title={t('alertingPanel.panel.actionDetails.testSuccessTitle')}
              data-testid={TEST_IDS.actionDetails.testSuccessMessage}
            >
              {t('alertingPanel.panel.actionDetails.testSuccessMessage')}
            </Alert>
          ) : (
            <Alert
              severity="error"
              title={t('alertingPanel.panel.actionDetails.testErrorTitle')}
              data-testid={TEST_IDS.actionDetails.testErrorMessage}
            >
              {testResult.message}
            </Alert>
          )}
        </>
      )}
      {isTesting && (
        <Alert
          severity="info"
          title={t('alertingPanel.panel.actionDetails.testProgressTitle')}
          data-testid={TEST_IDS.actionDetails.testProgressMessage}
        >
          {t('alertingPanel.panel.actionDetails.testProgressMessage')}
        </Alert>
      )}
      <footer className={styles.footer}>
        <Button variant="secondary" onClick={onClose} data-testid={TEST_IDS.actionDetails.buttonClose}>
          {t('alertingPanel.panel.actionDetails.closeButton')}
        </Button>
        <Button
          onClick={onTest}
          disabled={isInvalid || isTesting}
          icon="heart-rate"
          variant="destructive"
          data-testid={TEST_IDS.actionDetails.buttonTest}
        >
          {t('alertingPanel.panel.actionDetails.testButton')}
        </Button>
        <Button
          onClick={async () => {
            setSaving(true);
            await onSave(action);
            setSaving(false);
          }}
          disabled={isInvalid || saving}
          data-testid={TEST_IDS.actionDetails.buttonSave}
        >
          {t('alertingPanel.panel.actionDetails.saveButton')}
        </Button>
      </footer>
    </>
  );
};
