import { ActionRequestHeader } from '@bi/volkovlabs-engine-datasource';
import { Button, InlineField, InlineFieldRow, Input } from '@grafana/ui';
import { Collapse } from '@volkovlabs/components';
import React, { useCallback, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { TEST_IDS } from '../../constants';

/**
 * Properties
 */
interface Props {
  /**
   * Value
   *
   * @type {ActionRequestHeader[]}
   */
  value: ActionRequestHeader[];

  /**
   * Change
   */
  onChange: (value: ActionRequestHeader[]) => void;
}

/**
 * Action Headers Editor
 */
export const ActionHeadersEditor: React.FC<Props> = ({ value, onChange }) => {
  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * States
   */
  const [open, setOpen] = useState(true);

  /**
   * Add Item
   */
  const onAddItem = useCallback(() => {
    onChange(
      value.concat({
        key: '',
        value: '',
      })
    );
  }, [onChange, value]);

  /**
   * Change Item
   */
  const onChangeItem = useCallback(
    (updatedItem: ActionRequestHeader, indexToUpdate: number) => {
      onChange(value.map((item, index) => (index === indexToUpdate ? updatedItem : item)));
    },
    [onChange, value]
  );

  /**
   * Delete Item
   */
  const onDeleteItem = useCallback(
    (indexToDelete: number) => {
      onChange(value.filter((item, index) => index !== indexToDelete));
    },
    [onChange, value]
  );

  return (
    <Collapse
      title={t('actionHeadersEditor.label')}
      isOpen={open}
      onToggle={setOpen}
      fill="solid"
      headerTestId={TEST_IDS.actionHeadersEditor.rootHeader}
      contentTestId={TEST_IDS.actionHeadersEditor.rootContent}
    >
      <>
        {value.map((item, index) => (
          <InlineFieldRow key={index} data-testid={TEST_IDS.actionHeadersEditor.item(index)}>
            <InlineField grow={true} invalid={item.key.trim().length === 0}>
              <Input
                placeholder={t('actionHeadersEditor.key.placeholder')}
                value={item.key}
                onChange={(event) => {
                  onChangeItem(
                    {
                      ...item,
                      key: event.currentTarget.value,
                    },
                    index
                  );
                }}
                data-testid={TEST_IDS.actionHeadersEditor.fieldKey}
              />
            </InlineField>
            <InlineField grow={true}>
              <Input
                placeholder={t('actionHeadersEditor.value.placeholder')}
                value={item.value}
                onChange={(event) => {
                  onChangeItem(
                    {
                      ...item,
                      value: event.currentTarget.value,
                    },
                    index
                  );
                }}
                data-testid={TEST_IDS.actionHeadersEditor.fieldValue}
              />
            </InlineField>
            <Button
              icon="trash-alt"
              variant="secondary"
              onClick={() => onDeleteItem(index)}
              tooltip={t('actionHeadersEditor.deleteButton')}
              data-testid={TEST_IDS.actionHeadersEditor.buttonDelete}
            />
          </InlineFieldRow>
        ))}
        <Button icon="plus-circle" onClick={onAddItem} data-testid={TEST_IDS.actionHeadersEditor.buttonAdd}>
          {t('actionHeadersEditor.addButton')}
        </Button>
      </>
    </Collapse>
  );
};
