import { isGlobalAlertCondition } from '@bi/volkovlabs-alerting-panel/utils';
import {
  AlertConditionGlobal,
  AlertConditionItem,
  AlertConditionType,
  AlertEvaluationApplyMode,
  AlertVariable,
  AlertVariableValue,
  DashboardInfo,
  EditAlertData,
  EvaluationType,
  Threshold,
  VariableType,
} from '@bi/volkovlabs-engine-datasource';
import { VariableWithMultiSupport } from '@grafana/data';
import { config } from '@grafana/runtime';
import { GlobalVariableName, templateService } from '@volkovlabs/grafana-utils';
import { useEffect, useState } from 'react';
import { v4 as uuidv4 } from 'uuid';

/**
 * Use Alert Conditions
 */
export const useAlertConditions = ({ alert, dashboard }: { alert: EditAlertData; dashboard: DashboardInfo | null }) => {
  /**
   * States
   */
  const [conditions, setConditions] = useState(alert.conditions);
  const [initialized, setInitialized] = useState(false);
  const [thresholds, setThresholds] = useState<Threshold[]>([]);

  /**
   * Set Variables
   */
  useEffect(() => {
    const panel = dashboard?.panels.find((panel) => panel.id === alert.panelId);

    if (panel) {
      const dashboardVariables = dashboard?.templating.list || [];
      const allVariables = dashboardVariables.map(({ name }) => name);
      const variableNames: string[] = [];

      /**
       * Find used variable names
       */
      panel.targets?.forEach((target) => {
        Object.values(target).forEach((value: unknown) => {
          if (typeof value === 'string') {
            const containsTemplate = templateService.containsVariable(value, allVariables);
            if (containsTemplate) {
              variableNames.push(...templateService.getUsedVariables(value, true));
            }
          }
        });
      });

      const dependentVariables = [...new Set(variableNames).values()];

      /**
       * Find variable dependencies
       */
      while (dependentVariables.length) {
        const variableName = dependentVariables.shift();
        const dashboardVariable = dashboardVariables.find(({ name }) => name === variableName);

        if (!variableName || !dashboardVariable) {
          continue;
        }

        if (dashboardVariable.type === VariableType.QUERY) {
          if (
            typeof dashboardVariable.query === 'string' &&
            templateService.containsVariable(dashboardVariable.query, allVariables)
          ) {
            const usedVariables = templateService.getUsedVariables(dashboardVariable.query);
            dependentVariables.push(...usedVariables);
            variableNames.push(...usedVariables);
          }
        }
      }

      setConditions((previousConditions) => {
        const globalVariables = variableNames.reduce(
          (acc, variableName): Record<string, AlertVariable> => {
            const variable = dashboardVariables.find(({ name }) => name === variableName);
            const variableIndex = dashboardVariables.findIndex(({ name }) => name === variableName);

            const multi = (variable as VariableWithMultiSupport)?.multi || false;

            let value: AlertVariableValue = multi ? [] : '';

            const globalCondition = previousConditions.find(isGlobalAlertCondition);

            const alreadyExistValue = globalCondition?.variables?.[variableName]?.value;
            if (alreadyExistValue !== undefined) {
              value = alreadyExistValue;
            } else if (variable && 'current' in variable && variable?.current.value) {
              if (typeof variable.current.value === 'string' || Array.isArray(variable.current.value)) {
                value = variable.current.value;
              }
            }

            if (!variable) {
              if (variableName === GlobalVariableName.ORG) {
                return {
                  ...acc,
                  [variableName]: {
                    name: variableName,
                    multi: false,
                    value,
                    order: variableIndex,
                  },
                };
              }
              if (variableName === GlobalVariableName.USER) {
                return {
                  ...acc,
                  [variableName]: {
                    label: 'User',
                    name: variableName,
                    multi: false,
                    value: value || config.bootData.user.id.toString(),
                    order: variableIndex,
                  },
                };
              }

              return acc;
            }

            return {
              ...acc,
              [variableName]: {
                label: variable.label,
                name: variableName,
                multi,
                allowCustomValue: variable.type === 'textbox' || variable.type === 'constant',
                value,
                order: variableIndex,
              },
            };
          },
          {} as Record<string, AlertVariable>
        );

        /**
         * Global condition
         */
        const previousGlobalCondition = previousConditions.find(isGlobalAlertCondition);
        const globalCondition: AlertConditionGlobal = {
          type: AlertConditionType.GLOBAL,
          id: uuidv4(),
          repeat: null,
          evaluationType: EvaluationType.THRESHOLDS,
          evaluation: {
            type: EvaluationType.THRESHOLDS,
            colors: [],
          },
          ...previousGlobalCondition,
          variables: globalVariables,
          evaluationApply: {
            ...previousGlobalCondition?.evaluationApply,
            mode: previousGlobalCondition?.evaluationApply.mode || AlertEvaluationApplyMode.ALL,
            percentage: Math.ceil((previousGlobalCondition?.evaluationApply.percentage ?? 0) * 100),
          },
        };

        /**
         * Pre-define if panel repeated
         */
        globalCondition.repeat = globalCondition.repeat || panel.repeat || null;

        const conditions =
          previousConditions
            .filter((item): item is AlertConditionItem => !isGlobalAlertCondition(item))
            .map((item): AlertConditionItem => {
              return {
                ...item,
                variables: Object.keys(globalCondition.variables || {}).reduce(
                  (acc, variableName) => {
                    if (globalCondition.repeat === variableName && globalVariables[variableName]) {
                      const overrideValue = item.variables?.[variableName];
                      if (overrideValue) {
                        return {
                          ...acc,
                          [variableName]: overrideValue,
                        };
                      }
                    }
                    return acc;
                  },
                  {} as Record<string, AlertVariableValue>
                ),
              };
            }) || [];

        return [globalCondition, ...conditions];
      });
      setInitialized(true);
    }
  }, [alert.panelId, dashboard?.panels, dashboard?.templating.list]);

  /**
   * Set Thresholds
   */
  useEffect(() => {
    const panel = dashboard?.panels.find((panel) => panel.id === alert.panelId);

    if (panel) {
      const thresholds = panel.fieldConfig?.defaults.thresholds?.steps || [];

      setThresholds(
        thresholds.map((item) => ({
          ...item,
          value: item.value === undefined ? null : item.value,
        }))
      );
    }
  }, [alert.panelId, dashboard?.panels]);

  return {
    value: conditions,
    onChange: setConditions,
    initialized,
    thresholds,
  };
};
