import { getOptionsWithTestId } from '@bi/utils';
import { useAlertConditions } from '@bi/volkovlabs-alerting-panel/hooks';
import { isEvaluationType, isGlobalAlertCondition } from '@bi/volkovlabs-alerting-panel/utils';
import {
  Alert,
  AlertAnnotationMode,
  AlertConditionType,
  AlertEvaluationApplyMode,
  AlertStatus,
  AlertTimeRangeSource,
  DataSourceApi,
  EditAlertData,
  EvaluationType,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import { cx } from '@emotion/css';
import {
  Button,
  FieldSet,
  InlineField,
  InlineFieldRow,
  Input,
  RadioButtonGroup,
  RelativeTimeRangePicker,
  Select,
  useStyles2,
} from '@grafana/ui';
import { isValidCron } from 'cron-validator';
import cronstrue from 'cronstrue';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { v4 as uuidv4 } from 'uuid';

import { ALERT_TIME_RANGE_SOURCE_OPTIONS, ANNOTATION_OPTIONS, LABEL_WIDTH, TEST_IDS } from '../../constants';
import { AlertConditions } from '../AlertConditions';
import { getStyles } from './EditAlert.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * On Close
   */
  onClose: () => void;

  /**
   * On Save
   */
  onSave: (data: EditAlertData) => Promise<void>;

  /**
   * Initial
   */
  initial?: EditAlertData;
}

/**
 * Alert Details
 */
export const EditAlert: React.FC<Props> = ({ datasource, onSave, onClose, initial }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t, i18n } = useTranslation();

  /**
   * Alert
   */
  const [alert, setAlert] = useState<EditAlertData>(
    initial || {
      annotations: AlertAnnotationMode.DISABLED,
      dashboardId: null,
      id: '',
      panelId: null,
      schedule: '*/5 * * * *',
      status: AlertStatus.ACTIVE,
      timeRangeSource: AlertTimeRangeSource.DASHBOARD,
      title: '',
      actionIds: [],
      conditions: [
        {
          id: uuidv4(),
          type: AlertConditionType.GLOBAL,
          variables: {},
          repeat: null,
          evaluationType: EvaluationType.THRESHOLDS,
          evaluation: {
            type: EvaluationType.THRESHOLDS,
            colors: [],
          },
          evaluationApply: {
            mode: AlertEvaluationApplyMode.ALL,
            percentage: 0,
          },
        },
      ],
    }
  );

  /**
   * Saving
   */
  const [saving, setSaving] = useState(false);

  /**
   * Dashboards
   */
  const { data: dashboards, loading: isDashboardLoading } = useData({
    initial: [],
    query: useCallback((datasource) => datasource.api.grafana.getDashboards(), []),
    datasource,
  });

  /**
   * Dashboard Options
   */
  const dashboardOptions = useMemo(() => {
    return dashboards.map((dashboard) => ({
      value: dashboard.uid,
      label: dashboard.title,
    }));
  }, [dashboards]);

  /**
   * Dashboard Info
   */
  const { data: dashboardInfo, loading: isDashboardInfoLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (alert.dashboardId) {
          return datasource.api.grafana.getDashboardInfo(alert.dashboardId);
        }
        return null;
      },
      [alert.dashboardId]
    ),
    datasource,
  });

  /**
   * Actions
   */
  const { data: actions, loading: isActionsLoading } = useData({
    initial: [],
    query: useCallback(async (datasource) => {
      return datasource.api.actions.getAll();
    }, []),
    datasource,
  });

  /**
   * Default Action
   */
  const { data: defaultAction, loading: isDefaultActionLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (!alert.actionIds.length) {
          return datasource.api.actions.getDefault();
        }
        return null;
      },
      [alert.actionIds.length]
    ),
    datasource,
  });

  /**
   * Panel Options
   */
  const panelOptions = useMemo(() => {
    if (!dashboardInfo) {
      return [];
    }

    return dashboardInfo.panels
      .filter((panel) => panel.type !== 'row')
      .map((panel) => ({
        value: panel.id,
        label: `${panel.title || t('alertingPanel.panel.editAlert.panelOptions.untitled')} [${panel.type}]`,
      }));
  }, [dashboardInfo, t]);

  /**
   * Action Options
   */
  const actionOptions = useMemo(
    () =>
      actions.map((action) => ({
        value: action.id,
        label: action.title,
      })),
    [actions]
  );

  /**
   * Annotation Options
   */
  const annotationOptions = useMemo(() => {
    return getOptionsWithTestId(ANNOTATION_OPTIONS(t), TEST_IDS.editAlert.radioOption);
  }, [t]);

  /**
   * Schedule Description
   */
  const scheduleDescription = useMemo(() => {
    try {
      if (isValidCron(alert.schedule, { seconds: true, alias: true })) {
        return cronstrue.toString(alert.schedule, { locale: i18n.language });
      }
      return '';
    } catch {
      return '';
    }
  }, [alert.schedule, i18n.language]);

  /**
   * Alert Conditions
   */
  const {
    value: conditions,
    onChange: onChangeConditions,
    initialized: conditionsAvailable,
    thresholds,
  } = useAlertConditions({ alert, dashboard: dashboardInfo });

  /**
   * Is Valid
   */
  const isValid = useMemo(() => {
    if (!alert.title || !alert.panelId || !alert.schedule || !scheduleDescription) {
      return false;
    }

    if (alert.actionIds.length === 0 && !defaultAction) {
      return false;
    }

    const globalCondition = conditions.find(isGlobalAlertCondition);

    return conditions.every((condition) => {
      if (isGlobalAlertCondition(condition)) {
        const evaluation = condition.evaluation;

        /**
         * Validate if repeated variable set
         */
        if (condition.repeat) {
          const repeatedValue = condition.variables?.[condition.repeat]?.value;

          if (!repeatedValue) {
            return false;
          }

          if (Array.isArray(repeatedValue) && !repeatedValue.length) {
            return false;
          }
        }

        if (isEvaluationType(evaluation, EvaluationType.PATTERN) && evaluation.pattern.trim().length === 0) {
          return false;
        }

        return !(isEvaluationType(evaluation, EvaluationType.THRESHOLDS) && evaluation.colors.length === 0);
      }

      const evaluation = condition.evaluation;

      /**
       * Validate if repeated variable set
       */
      if (globalCondition?.repeat) {
        const repeatedValue = condition.variables?.[globalCondition.repeat];

        if (!repeatedValue) {
          return false;
        }

        if (Array.isArray(repeatedValue) && !repeatedValue.length) {
          return false;
        }
      }

      if (!evaluation || !evaluation.type) {
        return false;
      }

      if (isEvaluationType(evaluation, EvaluationType.PATTERN) && evaluation.pattern?.trim().length === 0) {
        return false;
      }

      if (isEvaluationType(evaluation, EvaluationType.THRESHOLDS)) {
        if (!evaluation.colors || evaluation.colors.length === 0) {
          return false;
        }
      }

      return true;
    });
  }, [
    alert.panelId,
    alert.schedule,
    alert.title,
    alert.actionIds.length,
    conditions,
    defaultAction,
    scheduleDescription,
  ]);

  /**
   * Time Range Source Options
   */
  const timeRangeSourceOptions = useMemo(() => {
    return getOptionsWithTestId(ALERT_TIME_RANGE_SOURCE_OPTIONS(t), TEST_IDS.editAlert.radioOption);
  }, [t]);

  return (
    <>
      <FieldSet data-testid={TEST_IDS.editAlert.root}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.titleLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <Input
            onChange={(event) => {
              setAlert({
                ...alert,
                title: event.currentTarget.value,
              });
            }}
            value={alert.title}
            data-testid={TEST_IDS.editAlert.fieldTitle}
          />
        </InlineField>

        <InlineField
          label={t('alertingPanel.panel.editAlert.scheduleLabel')}
          labelWidth={LABEL_WIDTH}
          grow={true}
          required={true}
        >
          <Input
            onChange={(event) => {
              setAlert({
                ...alert,
                schedule: event.currentTarget.value,
              });
            }}
            value={alert.schedule}
            placeholder={t('alertingPanel.panel.editAlert.schedulePlaceholder')}
            data-testid={TEST_IDS.editAlert.fieldSchedule}
          />
        </InlineField>
        <InlineField label={''} labelWidth={LABEL_WIDTH} transparent={true} grow={true} shrink={true}>
          <div
            className={cx(styles.helpText, {
              [styles.helpTextInvalid]: !scheduleDescription,
            })}
            data-testid={TEST_IDS.editAlert.scheduleDescription}
          >
            {scheduleDescription || t('alertingPanel.panel.editAlert.invalidSchedule')}
          </div>
        </InlineField>
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.targetLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.dashboardLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <Select
            onChange={(event) => {
              setAlert({
                ...alert,
                dashboardId: event.value!,
                panelId: null,
              });
            }}
            value={alert.dashboardId}
            options={dashboardOptions}
            isLoading={isDashboardLoading}
            data-testid={TEST_IDS.editAlert.fieldDashboard}
          />
        </InlineField>

        <InlineField
          label={t('alertingPanel.panel.editAlert.panelLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <Select
            onChange={(event) => {
              setAlert({
                ...alert,
                panelId: event.value!,
              });
            }}
            value={alert.panelId}
            options={panelOptions}
            isLoading={isDashboardInfoLoading}
            data-testid={TEST_IDS.editAlert.fieldPanel}
          />
        </InlineField>
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.timeRangeLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.timeRangeSourceLabel')}
          labelWidth={LABEL_WIDTH}
          required={true}
          data-testid={TEST_IDS.editAlert.fieldTimeRangeSource}
        >
          <RadioButtonGroup
            options={timeRangeSourceOptions}
            onChange={(value) => {
              setAlert({
                ...alert,
                timeRangeSource: value,
                timeRange:
                  value === AlertTimeRangeSource.DASHBOARD
                    ? undefined
                    : {
                        from: 600,
                        to: 0,
                      },
              });
            }}
            value={alert.timeRangeSource}
          />
        </InlineField>

        {alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED && alert.timeRange && (
          <InlineField
            label={t('alertingPanel.panel.editAlert.timeRangeLabel')}
            labelWidth={LABEL_WIDTH}
            required={true}
          >
            <RelativeTimeRangePicker
              timeRange={alert.timeRange}
              onChange={(event) => {
                setAlert({
                  ...alert,
                  timeRange: event,
                });
              }}
              data-testid={TEST_IDS.editAlert.fieldTimeRange}
            />
          </InlineField>
        )}

        {alert.timeRangeSource === AlertTimeRangeSource.DASHBOARD && dashboardInfo?.time && (
          <InlineFieldRow data-testid={TEST_IDS.editAlert.dashboardTimeRange}>
            <InlineField label={t('alertingPanel.panel.editAlert.timeRange.from')} labelWidth={LABEL_WIDTH} disabled>
              <Input value={dashboardInfo?.time.from} data-testid={TEST_IDS.editAlert.fieldDashboardTimeRangeFrom} />
            </InlineField>
            <InlineField label={t('alertingPanel.panel.editAlert.timeRange.to')} labelWidth={LABEL_WIDTH} grow disabled>
              <Input value={dashboardInfo?.time.to} data-testid={TEST_IDS.editAlert.fieldDashboardTimeRangeTo} />
            </InlineField>
          </InlineFieldRow>
        )}
      </FieldSet>

      {!!conditions && (
        <AlertConditions
          value={conditionsAvailable ? conditions : []}
          onChange={onChangeConditions}
          datasource={datasource}
          dashboard={dashboardInfo}
          timeRange={alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED ? alert.timeRange : undefined}
          editable={conditionsAvailable}
          thresholds={thresholds}
        />
      )}

      <FieldSet label={t('alertingPanel.panel.editAlert.actionLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.actionsLabel')}
          labelWidth={LABEL_WIDTH}
          grow={true}
          invalid={alert.actionIds.length > 0 ? false : !defaultAction}
          error={t('alertingPanel.panel.editAlert.noActionsError')}
        >
          <Select
            onChange={(event) => {
              const options = Array.isArray(event) ? event : [event!];
              setAlert({
                ...alert,
                actionIds: options.map(({ value }) => value).filter((value) => !!value),
              });
            }}
            value={alert.actionIds}
            options={actionOptions}
            isMulti={true}
            isLoading={isActionsLoading || isDefaultActionLoading}
            placeholder={
              defaultAction
                ? t('alertingPanel.panel.editAlert.actionsPlaceholder', { name: defaultAction.title })
                : t('alertingPanel.panel.editAlert.actionsNoDefaultMessage')
            }
            data-testid={TEST_IDS.editAlert.fieldActions}
          />
        </InlineField>
        <InlineField
          label={t('alertingPanel.panel.editAlert.annotationsLabel')}
          labelWidth={LABEL_WIDTH}
          grow={true}
          data-testid={TEST_IDS.editAlert.fieldAnnotations}
        >
          <RadioButtonGroup
            options={annotationOptions}
            onChange={(value) => {
              setAlert((alert) => ({
                ...alert,
                annotations: value,
              }));
            }}
            value={alert.annotations}
          />
        </InlineField>
      </FieldSet>

      <footer className={styles.footer}>
        <Button variant="secondary" onClick={onClose} data-testid={TEST_IDS.editAlert.buttonClose}>
          {t('alertingPanel.panel.editAlert.closeButton')}
        </Button>
        <Button
          onClick={async () => {
            setSaving(true);
            await onSave({
              ...(alert as Alert),
              conditions: conditions.map((condition) => {
                if (isGlobalAlertCondition(condition)) {
                  return {
                    ...condition,
                    evaluationApply: {
                      ...condition.evaluationApply,
                      percentage: condition.evaluationApply.percentage / 100,
                    },
                  };
                }

                return condition;
              }),
            });
            setSaving(false);
          }}
          disabled={!isValid || saving}
          data-testid={TEST_IDS.editAlert.buttonSave}
        >
          {t('alertingPanel.panel.editAlert.saveButton')}
        </Button>
      </footer>
    </>
  );
};
