import { useStyles2 } from '@grafana/ui';
import React from 'react';

import { TEST_IDS } from '../../constants';
import { Tag, TagName } from './Tag';
import { getStyles } from './TagList.styles';

/**
 * Tag Item
 */
export interface TagItem {
  /**
   * Name
   *
   * @type {TagName}
   */
  name?: TagName;

  /**
   * Text
   *
   * @type {string}
   */
  text: string;
}

/**
 * Properties
 */
interface Props {
  /**
   * Tags
   *
   * @type {TagItem[]}
   */
  tags: TagItem[];
}

/**
 * Tag List
 */
export const TagList: React.FC<Props> = ({ tags }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  return (
    <ul className={styles.root} data-testid={TEST_IDS.tagList.root}>
      {tags.map((tag, index) => (
        <li key={index} data-testid={TEST_IDS.tagList.item(tag.name)}>
          <Tag {...tag} index={index} />
        </li>
      ))}
    </ul>
  );
};
