/**
 * Action Type
 */
export enum ActionType {
  REQUEST = 'request',

  /**
   * Custom for testing purpose
   */
  CUSTOM = 'custom',
}

/**
 * Action Method
 */
export enum ActionMethod {
  POST = 'POST',
}

/**
 * Action Base
 */
interface ActionBase {
  /**
   * ID
   *
   * @type {string}
   */
  id: string;

  /**
   * Title
   *
   * @type {string}
   */
  title: string;

  /**
   * Type
   *
   * @type {ActionType}
   */
  type: ActionType;

  /**
   * Is Default
   *
   * @type {boolean}
   */
  isDefault: boolean;

  /**
   * Use For
   *
   * @type {string[]}
   */
  useFor?: string[];

  /**
   * Use For By Default
   *
   * @type {string[]}
   */
  useForByDefault?: string[];

  /**
   * Message
   *
   * @type {string}
   */
  message: string;
}

/**
 * Action Request Header
 */
export interface ActionRequestHeader {
  /**
   * Key
   *
   * @type {string}
   */
  key: string;

  /**
   * Value
   *
   * @type {string}
   */
  value: string;
}

/**
 * Action Request Options
 */
export interface ActionRequestOptions {
  /**
   * URL
   *
   * @type {string}
   */
  url: string;

  /**
   * Headers
   */
  headers: ActionRequestHeader[];
}

/**
 * Action
 */
export type Action = ActionBase &
  (
    | {
        type: ActionType.REQUEST;
        options: ActionRequestOptions;
      }
    | { type: ActionType.CUSTOM; options: object }
  );

/**
 * Edit Action Data
 */
export interface EditActionData extends Omit<Action, 'useFor'> {}
