import { AlertCondition, AlertConditionType, AlertVariable } from '@bi/volkovlabs-engine-datasource';

/**
 * Is Global Alert Condition
 */
export const isGlobalAlertCondition = (
  condition: AlertCondition
): condition is AlertCondition & { type: AlertConditionType.GLOBAL } => {
  return condition.type === AlertConditionType.GLOBAL;
};

/**
 * To Variables Map
 */
export const toVariablesMap = (variables: Record<string, AlertVariable | string | string[]>) =>
  Object.entries(variables).reduce((acc, [name, value]) => {
    const variableValue = typeof value === 'object' && 'name' in value ? value.value : value;

    if (!variableValue || (Array.isArray(variableValue) && variableValue.length === 0)) {
      return acc;
    }

    return {
      ...acc,
      [name]: variableValue,
    };
  }, {});
