import { Tag as TagBase } from '@grafana/ui';
import React from 'react';

export enum TagName {
  STATE_OK = 'stateOk',
  STATE_SCHEDULED = 'stateScheduled',
  STATE_UNKNOWN = 'stateUnknown',
  STATE_ALERTING = 'stateAlerting',
  STATE_ERROR = 'stateError',
  TARGET_STATUS_INVALID = 'targetStatusInvalid',
  STATUS_ACTIVE = 'statusActive',
  STATUS_PAUSED = 'statusPaused',
  ACTION_REQUEST = 'actionRequest',
  ACTION_TEST = 'actionTest',
  ACTION_DEFAULT = 'actionDefault',
}

/**
 * Properties
 */
interface Props {
  /**
   * Name
   *
   * @type {TagName}
   */
  name?: TagName;

  /**
   * Text
   *
   * @type {string}
   */
  text: string;

  /**
   * Index
   *
   * @type {number}
   */
  index: number;
}

/**
 * Get Color Index
 * Index Colors - https://github.com/grafana/grafana/blob/55106c6ba89d11ae576f8cbe430ecfb3ad6d99ee/packages/grafana-ui/src/utils/tags.ts#L1C7-L1C17
 */
const getColorIndex = (index: number, name?: TagName): number | undefined => {
  if (!name) {
    return;
  }

  switch (name) {
    case TagName.STATE_OK:
    case TagName.STATUS_ACTIVE: {
      return 4;
    }
    case TagName.STATUS_PAUSED: {
      return 9;
    }
    case TagName.STATE_UNKNOWN: {
      return 14;
    }
    case TagName.STATE_ALERTING: {
      return 7;
    }
    case TagName.STATE_SCHEDULED: {
      return 1;
    }
    case TagName.STATE_ERROR: {
      return 0;
    }
    case TagName.TARGET_STATUS_INVALID: {
      return 25;
    }
    case TagName.ACTION_REQUEST: {
      return 4;
    }
    case TagName.ACTION_TEST: {
      return 14;
    }
    case TagName.ACTION_DEFAULT: {
      return 1;
    }
    default: {
      return;
    }
  }
};

/**
 * Tag
 */
export const Tag: React.FC<Props> = ({ name, text, index }) => {
  return <TagBase colorIndex={getColorIndex(index, name)} name={text} />;
};
