import {
  AlertCondition as AlertConditionData,
  AlertConditionItem,
  AlertConditionType,
  AlertEvaluationApplyMode,
  AlertTimeRange,
  ApiName,
  DashboardInfo,
  DataSourceApi,
  getApiMessage,
  notifyError,
  Threshold,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import { SelectableValue } from '@grafana/data';
import { Alert, Button, FieldSet, IconButton, useStyles2 } from '@grafana/ui';
import { Collapse } from '@volkovlabs/components';
import { GlobalVariableName } from '@volkovlabs/grafana-utils';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { v4 as uuidv4 } from 'uuid';

import { TEST_IDS, THRESHOLDS_APPLY_MODE_OPTIONS } from '../../constants';
import { isGlobalAlertCondition, toVariablesMap } from '../../utils';
import { AlertCondition } from '../AlertCondition';
import { getStyles } from './AlertConditions.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Value
   *
   * @type {AlertConditionData[]}
   */
  value: AlertConditionData[];

  /**
   * Change
   */
  onChange: (value: AlertConditionData[]) => void;

  /**
   * Editable
   *
   * @type {boolean}
   */
  editable?: boolean;

  /**
   * Datasource
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * Dashboard
   *
   * @type {DashboardInfo | null}
   */
  dashboard: DashboardInfo | null;

  /**
   * Time Range
   *
   * @type {AlertTimeRange}
   */
  timeRange?: AlertTimeRange;

  /**
   * Thresholds
   *
   * @type {Threshold[]}
   */
  thresholds: Threshold[];
}

/**
 * Alert Conditions
 */
export const AlertConditions: React.FC<Props> = ({
  value,
  onChange,
  editable = true,
  datasource,
  dashboard,
  timeRange,
  thresholds,
}) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * States
   */
  const [expanded, setExpanded] = useState<Record<string, boolean>>({});

  /**
   * On Change Item
   */
  const onChangeItem = useCallback(
    (updatedItem: AlertConditionData) => {
      /**
       * Remove conditions if no repeat
       */
      if (isGlobalAlertCondition(updatedItem) && updatedItem.repeat === null) {
        onChange([updatedItem]);
        return;
      }

      onChange(
        value.map((item) => {
          if (item.id === updatedItem.id) {
            return updatedItem;
          }

          return item;
        })
      );
    },
    [onChange, value]
  );

  /**
   * Global Condition
   */
  const globalCondition = useMemo(() => {
    return value.find(isGlobalAlertCondition);
  }, [value]);

  /**
   * Variable Names
   */
  const variableNames = useMemo(() => {
    if (!globalCondition) {
      return [];
    }

    return Object.values(globalCondition.variables || {})
      .sort((a, b) => (a.order > b.order ? 1 : -1))
      .map(({ name }) => name)
      .filter((name) => name !== GlobalVariableName.ORG);
  }, [globalCondition]);

  /**
   * Contain user variable
   */
  const containUserVariable = useMemo(() => {
    return variableNames.some((name) => name === GlobalVariableName.USER);
  }, [variableNames]);

  /**
   * Load Org Users
   */
  const { data: users } = useData({
    initial: [],
    query: useCallback(
      async (datasource) => {
        if (containUserVariable) {
          return datasource.api.grafana.getOrgUsers();
        }

        return;
      },
      [containUserVariable]
    ),
    datasource,
  });

  /**
   * Overrides
   */
  const items = useMemo(() => {
    return value.filter((alert): alert is AlertConditionItem => alert.type === AlertConditionType.ITEM);
  }, [value]);

  /**
   * Thresholds Options
   */
  const thresholdsOptions: SelectableValue[] = useMemo(() => {
    return thresholds.map(({ color, value }) => ({
      value: color,
      label: value !== null ? value.toString() : 'Base',
    }));
  }, [thresholds]);

  /**
   * Evaluation Apply Mode Options
   */
  const evaluationApplyModeOptions: Array<SelectableValue<AlertEvaluationApplyMode>> = useMemo(() => {
    return THRESHOLDS_APPLY_MODE_OPTIONS(t);
  }, [t]);

  /**
   * Load Variable Options
   */
  const {
    data: loadedOptions,
    loading: isOptionsLoading,
    loaded: isOptionsLoaded,
    error: isOptionsError,
  } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (dashboard && globalCondition && Object.keys(globalCondition.variables || {}).length) {
          try {
            return await datasource.api.grafana.getVariableOptions(
              dashboard.uid,
              toVariablesMap(globalCondition.variables || {}),
              timeRange
            );
          } catch (e) {
            notifyError([t('api.error'), getApiMessage(ApiName.GET_VARIABLE_OPTIONS)]);
            throw e;
          }
        }

        return;
      },
      [dashboard, globalCondition, t, timeRange]
    ),
    datasource,
  });

  /**
   * Get Condition Title
   */
  const getConditionTitle = useCallback(
    (item: AlertConditionItem, index: number): string => {
      const value = globalCondition?.repeat ? item.variables?.[globalCondition?.repeat] : '';

      if (Array.isArray(value)) {
        return `[${value.join(', ')}]`;
      }

      return value?.toString() || `#${index + 1}`;
    },
    [globalCondition?.repeat]
  );

  return (
    <>
      {globalCondition && (
        <AlertCondition
          globalItem={globalCondition}
          item={globalCondition}
          variableNames={variableNames}
          editable={editable}
          isGlobal={true}
          onChange={onChangeItem}
          users={users}
          loadedOptions={loadedOptions}
          isOptionsLoaded={isOptionsLoaded}
          isOptionsLoading={isOptionsLoading}
          isOptionsError={isOptionsError}
          thresholdsOptions={thresholdsOptions}
          evaluationApplyModeOptions={evaluationApplyModeOptions}
        >
          {(content) => content()}
        </AlertCondition>
      )}
      {!!globalCondition?.repeat && (
        <FieldSet label={t('alert.conditions.sectionLabel')} data-testid={TEST_IDS.alertConditions.itemsList}>
          {globalCondition && items.length ? (
            items.map((item, index) => {
              return (
                <div className={styles.item} key={item.id} data-testid={TEST_IDS.alertConditions.item(item.id)}>
                  <AlertCondition
                    globalItem={globalCondition}
                    item={item}
                    variableNames={variableNames}
                    editable={editable}
                    isGlobal={false}
                    onChange={onChangeItem}
                    users={users}
                    loadedOptions={loadedOptions}
                    isOptionsLoading={isOptionsLoading}
                    isOptionsLoaded={isOptionsLoaded}
                    isOptionsError={isOptionsError}
                    thresholdsOptions={thresholdsOptions}
                    evaluationApplyModeOptions={evaluationApplyModeOptions}
                  >
                    {(content) => (
                      <Collapse
                        title={getConditionTitle(item, index)}
                        actions={
                          <>
                            {editable && (
                              <IconButton
                                name="trash-alt"
                                onClick={() => onChange(value.filter((condition) => condition.id !== item.id))}
                                aria-label=""
                                tooltip={t('alert.conditions.buttonDelete')}
                                data-testid={TEST_IDS.alertConditions.buttonDeleteItem}
                              />
                            )}
                          </>
                        }
                        isOpen={expanded[item.id]}
                        onToggle={(isOpen) =>
                          setExpanded({
                            ...expanded,
                            [item.id]: isOpen,
                          })
                        }
                        headerTestId={TEST_IDS.alertConditions.itemHeader(item.id)}
                        contentTestId={TEST_IDS.alertConditions.itemContent(item.id)}
                      >
                        <div className={styles.itemContent}>{content()}</div>
                      </Collapse>
                    )}
                  </AlertCondition>
                </div>
              );
            })
          ) : (
            <Alert
              severity="info"
              title={t('alert.conditions.noItemsTitle')}
              data-testid={TEST_IDS.alertConditions.noItemsMessage}
            >
              {t('alert.conditions.noItemsMessage')}
            </Alert>
          )}

          {editable && (
            <Button
              icon="plus-circle"
              onClick={() => {
                const id = uuidv4();
                onChange(
                  value.concat({
                    id,
                    type: AlertConditionType.ITEM,
                    repeat: null,
                  })
                );
                setExpanded({
                  ...expanded,
                  [id]: true,
                });
              }}
              data-testid={TEST_IDS.alertConditions.buttonAddItem}
            >
              {t('alert.conditions.buttonAdd')}
            </Button>
          )}
        </FieldSet>
      )}
    </>
  );
};
