import { ALL_VARIABLE_TEXT, ALL_VARIABLE_VALUE } from '@bi/constants';
import { getOptionsWithTestId } from '@bi/utils';
import { isEvaluationType, isGlobalAlertCondition } from '@bi/volkovlabs-alerting-panel/utils';
import {
  AlertCondition as AlertConditionData,
  AlertConditionGlobal,
  AlertConditionItem,
  AlertEvaluation,
  AlertEvaluationApplyMode,
  AlertEvaluationPattern,
  AlertEvaluationThresholds,
  AlertVariable,
  AlertVariableOption,
  AlertVariableValue,
  EvaluationType,
  UserDetails,
} from '@bi/volkovlabs-engine-datasource';
import { SelectableValue } from '@grafana/data';
import { FieldSet, InlineField, Input, RadioButtonGroup, Select, useStyles2, useTheme2 } from '@grafana/ui';
import { Slider } from '@volkovlabs/components';
import { GlobalVariableName } from '@volkovlabs/grafana-utils';
import { omit } from 'lodash';
import React, { useCallback, useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { EVALUATION_TYPE_OPTIONS, LABEL_WIDTH, TEST_IDS } from '../../constants';
import { getStyles } from './AlertCondition.styles';
import { addAllOption, isMultiValue, isValueExist, removeAllOption } from './utils';

/**
 * Props
 */
interface Props<TItem = AlertConditionData> {
  /**
   * Global Condition
   *
   * @type {AlertConditionGlobal}
   */
  globalItem: AlertConditionGlobal;

  /**
   * Condition
   *
   * @type {AlertConditionData}
   */
  item: TItem;

  /**
   * Variable Names
   *
   * @type {string[]}
   */
  variableNames: string[];

  /**
   * Editable
   *
   * @type {boolean}
   */
  editable: boolean;

  /**
   * Is Global
   *
   * @type {boolean}
   */
  isGlobal: boolean;

  /**
   * On Change
   */
  onChange: (item: TItem) => void;

  /**
   * Users
   *
   * @type {UserDetails[]}
   */
  users: UserDetails[];

  /**
   * Children
   */
  children: (content: () => React.ReactNode) => React.ReactNode;

  /**
   * Loaded Options
   */
  loadedOptions: Record<string, AlertVariableOption[]> | null;

  /**
   * Is Options Loading
   *
   * @type {boolean}
   */
  isOptionsLoading: boolean;

  /**
   * Is Options Loaded
   *
   * @type {boolean}
   */
  isOptionsLoaded: boolean;

  /**
   * Is Options Error
   *
   * @type {boolean}
   */
  isOptionsError: boolean;

  /**
   * Thresholds
   *
   * @type {SelectableValue[]}
   */
  thresholdsOptions: SelectableValue[];

  /**
   * Evaluation Apply Mode Options
   *
   * @type {SelectableValue<AlertEvaluationApplyMode>[]}
   */
  evaluationApplyModeOptions: Array<SelectableValue<AlertEvaluationApplyMode>>;
}

/**
 * Alert Condition
 */
export const AlertCondition: React.FC<Props> = ({
  variableNames,
  globalItem,
  item,
  isGlobal,
  editable,
  onChange,
  users,
  children,
  loadedOptions,
  isOptionsLoading,
  isOptionsLoaded,
  isOptionsError,
  thresholdsOptions,
  evaluationApplyModeOptions,
}) => {
  /**
   * Styles and theme
   */
  const styles = useStyles2(getStyles);
  const theme = useTheme2();

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Evaluation Type Options
   */
  const evaluationTypeOptions = useMemo(() => {
    return getOptionsWithTestId(EVALUATION_TYPE_OPTIONS(t), TEST_IDS.alertConditions.evaluationOption);
  }, [t]);

  /**
   * Condition loaded options
   */
  const conditionLoadedOptions = useMemo(() => {
    /**
     * Return all options for global
     */
    if (isGlobal) {
      return loadedOptions;
    }

    /**
     * Return no variables if no repeat or repeated variable doesn't exist
     */
    if (!globalItem.repeat || !globalItem.variables?.[globalItem.repeat]) {
      return {};
    }

    const repeatValue = globalItem.variables?.[globalItem.repeat].value;
    const repeatValueArray = (Array.isArray(repeatValue) ? repeatValue : [repeatValue]).filter(
      (item) => item !== null
    ) as Array<string | number>;

    const options = loadedOptions?.[globalItem.repeat];

    /**
     * Custom value or unable to load options so take entered values
     */
    if (!options) {
      return {
        [globalItem.repeat]: repeatValueArray.map((value) => ({
          value,
          label: value.toString(),
        })),
      };
    }

    /**
     * Return options for repeated variable
     */
    return {
      [globalItem.repeat]: repeatValueArray.some((value) => value === ALL_VARIABLE_VALUE)
        ? removeAllOption(options)
        : repeatValueArray.map((value) => {
            const option = options.find((option) => option.value === value);

            return {
              value: option?.value || value,
              label: option?.label || value.toString(),
            };
          }),
    };
  }, [globalItem.repeat, globalItem.variables, isGlobal, loadedOptions]);

  /**
   * On Change Variable Value
   */
  const onChangeVariableValue = useCallback(
    (event: { name: string; value: string | Array<string | number> | undefined | number }) => {
      if (isGlobalAlertCondition(item)) {
        const variable = item.variables?.[event.name];

        if (!variable) {
          onChange(item);
          return;
        }

        onChange({
          ...item,
          variables: {
            ...(item.variables || {}),
            [variable.name]: {
              ...variable,
              value: event.value ? event.value : null,
            },
          },
        });
        return;
      }

      onChange({
        ...item,
        variables: {
          ...item.variables,
          [event.name]: event.value ? event.value : '',
        },
      });
    },
    [item, onChange]
  );

  /**
   * Get Variable Options
   */
  const getVariableOptions = useCallback(
    (variable: AlertVariable, repeat: boolean): Array<SelectableValue<string | number>> => {
      /**
       * Allow to select any org users
       */
      if (variable.name === GlobalVariableName.USER) {
        return users.map((user) => ({
          label: user.login,
          value: user.userId.toString(),
        }));
      }

      /**
       * Loaded Options
       */
      const loadedOptions = conditionLoadedOptions?.[variable.name];

      /**
       * Allow only loaded options
       */
      if (!variable.allowCustomValue && loadedOptions) {
        /**
         * Variable is not repeated so return options as is
         */
        if (!repeat) {
          return loadedOptions;
        }

        /**
         * Add All option for repeated variable
         */
        return addAllOption(loadedOptions);
      }

      const itemVariable = item.variables?.[variable.name];

      if (!itemVariable) {
        return loadedOptions || [];
      }

      const value = typeof itemVariable === 'object' && 'name' in itemVariable ? itemVariable.value : itemVariable;

      /**
       * Multi Custom Values
       */
      if (isMultiValue(variable, globalItem.repeat)) {
        const arrayValue = Array.isArray(value) ? value : [value];

        return arrayValue
          .filter(isValueExist)
          .map((value) => ({
            value,
            label: value.toString(),
          }))
          .concat((loadedOptions || []).filter((item) => !arrayValue.includes(item.value)));
      }

      /**
       * Single Custom Value
       */
      const simpleValue = Array.isArray(value) ? value[0] : value;

      /**
       * Empty value
       */
      if (!isValueExist(simpleValue)) {
        return loadedOptions || [];
      }

      return [
        {
          label: simpleValue.toString(),
          value: simpleValue,
        },
      ].concat((loadedOptions || []).filter((item) => item.value !== simpleValue));
    },
    [conditionLoadedOptions, globalItem, item.variables, users]
  );

  /**
   * Validate values to availability
   */
  useEffect(() => {
    if (editable && (isOptionsLoaded || isOptionsError)) {
      let shouldUpdate = false;
      const updatedVariables: Record<string, AlertVariableValue | AlertVariableValue[]> = {};

      for (const name of variableNames) {
        const variable = globalItem.variables?.[name];

        /**
         * No variable
         */
        if (!variable) {
          continue;
        }

        const variableOptions = getVariableOptions(variable, globalItem.repeat === variable.name);

        const variableValue: AlertVariableValue | undefined = isGlobalAlertCondition(item)
          ? variable.value
          : item.variables?.[name];

        if (variableValue === undefined) {
          continue;
        }

        const isMulti = isMultiValue(variable, globalItem.repeat);

        /**
         * Multi value
         */
        if (isMulti) {
          updatedVariables[name] = Array.isArray(variableValue)
            ? variableValue
            : [variableValue].filter((value) => !!value);
        } else {
          /**
           * Single value
           */
          updatedVariables[name] = Array.isArray(variableValue) ? variableValue[0] || '' : variableValue;
        }

        const valueArray = Array.isArray(variableValue) ? variableValue : [variableValue].filter((value) => !!value);

        const availableValues = valueArray.filter((value) => variableOptions.some((item) => item.value === value));

        /**
         * Set available values
         */
        if (
          availableValues.length !== valueArray.length ||
          (isMulti && !Array.isArray(variableValue)) ||
          (!isMulti && Array.isArray(variable))
        ) {
          shouldUpdate = true;

          if (availableValues.length > 0) {
            updatedVariables[name] = isMulti ? availableValues : availableValues[0];
          } else {
            updatedVariables[name] = isMulti ? [] : '';
          }
        }
      }

      /**
       * Selected values are unavailable
       */
      if (shouldUpdate) {
        /**
         * Update global variables
         */
        if (isGlobalAlertCondition(item)) {
          onChange({
            ...item,
            variables: Object.entries(item?.variables || {}).reduce(
              (acc, [name, variable]) => ({
                ...acc,
                [name]: {
                  ...variable,
                  value: updatedVariables[name],
                },
              }),
              {}
            ),
          });
          return;
        }

        /**
         * Update variables override
         */
        onChange({
          ...item,
          variables: Object.entries(updatedVariables).reduce((acc, [name, value]) => {
            if ((Array.isArray(value) && value.length === 0) || !value || name !== globalItem.repeat) {
              return acc;
            }
            return {
              ...acc,
              [name]: value,
            };
          }, {}),
        } as AlertConditionItem);
      }
    }
  }, [
    getVariableOptions,
    globalItem.variables,
    isGlobal,
    item,
    item.variables,
    isOptionsLoaded,
    onChange,
    variableNames,
    editable,
    globalItem.repeat,
    isOptionsError,
  ]);

  /**
   * Reset evaluation overrides
   */
  useEffect(() => {
    if (!isGlobalAlertCondition(item) && item.evaluation && globalItem.evaluation.type !== item.evaluation?.type) {
      onChange(omit(item, ['evaluation']));
    }
  }, [globalItem, item, onChange]);

  /**
   * Variable Items
   */
  const variableItems = useMemo(() => {
    return variableNames
      .map((name) => {
        const variable = globalItem.variables?.[name];

        if (!variable) {
          return null;
        }
        return variable;
      })
      .filter((item): item is AlertVariable => {
        if (isGlobal) {
          return !!item;
        }

        return item?.name === globalItem.repeat;
      })
      .map((variable) => ({
        name: variable.name,
        label: variable.label || variable.name,
        variable,
      }));
  }, [globalItem.repeat, globalItem.variables, isGlobal, variableNames]);

  /**
   * Items to repeat
   */
  const itemsToRepeat = useMemo(() => {
    return variableItems.map(({ name, label }) => ({
      label,
      value: name,
    }));
  }, [variableItems]);

  /**
   * Allow Custom Value if enabled or unable to load options
   */
  const hasAllowCustomValue = useCallback(
    (variable: AlertVariable) => {
      return variable.allowCustomValue || !conditionLoadedOptions?.[variable.name];
    },
    [conditionLoadedOptions]
  );

  const allEvaluation: AlertEvaluation = {
    ...globalItem.evaluation,
    ...(item.evaluation as AlertEvaluation),
  };

  /**
   * Reset value for override
   */
  const onResetValueInOverride = useCallback(
    (name: Exclude<keyof AlertEvaluationPattern | keyof AlertEvaluationThresholds, 'type'>) => {
      /**
       * Check if not global condition or not type key
       */
      if (isGlobalAlertCondition(item)) {
        return;
      }

      const result = omit(item.evaluation, name);

      if (Object.keys(result).length === 1) {
        /**
         * No overrides so just removes evaluation
         */
        onChange(omit(item, 'evaluation'));
      } else {
        /**
         * Other overrides exist so clean value
         */
        onChange({
          ...item,
          evaluation: omit(item.evaluation, name),
        });
      }
    },
    [item, onChange]
  );

  return children(() => (
    <>
      {variableItems.length > 0 && (
        <>
          {isGlobal && (
            <FieldSet label={t('alert.conditions.repeatOptions.label')}>
              <InlineField
                label={t('alert.conditions.repeatOptions.repeatByLabel')}
                labelWidth={LABEL_WIDTH}
                grow={true}
                disabled={!editable}
                className={styles.selectField}
              >
                <Select
                  onChange={(event) => {
                    onChange({
                      ...item,
                      repeat: event?.value ?? null,
                    });
                  }}
                  options={itemsToRepeat}
                  value={item.repeat}
                  isClearable={true}
                  data-testid={TEST_IDS.alertConditions.fieldRepeat}
                />
              </InlineField>
            </FieldSet>
          )}
          <FieldSet
            label={isGlobal ? t('alert.conditions.globalVariablesLabel') : t('alert.conditions.variablesLabel')}
          >
            {variableItems.map(({ name, variable, label }) => {
              const variableValue = isGlobal ? variable.value : (item.variables?.[name] as AlertVariableValue);
              const valueArray = Array.isArray(variable.value) ? variable.value : [variable.value];
              const options = getVariableOptions(variable, item.repeat === variable.name);
              const labels = valueArray.map(
                (value) =>
                  options.find((option) => option.value === value)?.label ||
                  (value === ALL_VARIABLE_VALUE ? ALL_VARIABLE_TEXT : value)
              );

              const placeholder = labels.join(', ');
              const isRequired = name === globalItem.repeat;
              const isEmptyVariableValueError =
                isRequired && (Array.isArray(variableValue) ? !variableValue.length : !variableValue);

              return (
                <InlineField
                  key={name}
                  label={label}
                  labelWidth={LABEL_WIDTH}
                  grow={true}
                  required={isRequired}
                  disabled={!editable}
                  invalid={isEmptyVariableValueError}
                  error={t('alert.conditions.noVariableValueError')}
                  className={styles.selectField}
                >
                  <Select
                    onChange={(event: SelectableValue<string | number> | undefined) => {
                      /**
                       * Multi value
                       */
                      if (Array.isArray(event)) {
                        /**
                         * Reset value
                         */
                        if (!event.length) {
                          onChangeVariableValue({
                            name,
                            value: undefined,
                          });
                        }

                        const previousValueArray = Array.isArray(variableValue) ? variableValue : [variableValue];
                        const containAll = previousValueArray.includes(ALL_VARIABLE_VALUE);

                        /**
                         * Set only all
                         */
                        if (!containAll && event.some((item) => item.value === ALL_VARIABLE_VALUE)) {
                          onChangeVariableValue({
                            name,
                            value: [ALL_VARIABLE_VALUE],
                          });
                          return;
                        }

                        /**
                         * Remove all
                         */
                        if (containAll && event.some((item) => item.value === ALL_VARIABLE_VALUE)) {
                          onChangeVariableValue({
                            name,
                            value: event.filter((item) => item.value !== ALL_VARIABLE_VALUE).map(({ value }) => value),
                          });
                          return;
                        }

                        onChangeVariableValue({
                          name,
                          value: event.map(({ value }) => value),
                        });
                        return;
                      }

                      /**
                       * Single value
                       */
                      onChangeVariableValue({
                        name,
                        value: event?.value,
                      });
                    }}
                    isMulti={isMultiValue(variable, globalItem.repeat)}
                    value={variableValue ?? null}
                    options={options}
                    allowCustomValue={hasAllowCustomValue(variable)}
                    isClearable={!isRequired}
                    placeholder={placeholder}
                    isLoading={isOptionsLoading}
                    data-testid={TEST_IDS.alertConditions.fieldVariable(name)}
                  />
                </InlineField>
              );
            })}
          </FieldSet>
        </>
      )}

      <FieldSet label={t('alert.conditions.evaluationLabel')}>
        {isGlobalAlertCondition(item) && (
          <InlineField
            label={t('alert.conditions.evaluationTypeLabel')}
            labelWidth={LABEL_WIDTH}
            disabled={!editable}
            data-testid={TEST_IDS.alertConditions.fieldEvaluationType}
          >
            <RadioButtonGroup
              options={evaluationTypeOptions}
              onChange={(value) => {
                onChange({
                  ...item,
                  evaluationType: value,
                  evaluation:
                    value === EvaluationType.PATTERN
                      ? {
                          type: EvaluationType.PATTERN,
                          field: '',
                          pattern: '',
                        }
                      : {
                          type: EvaluationType.THRESHOLDS,
                          colors: [],
                        },
                });
              }}
              value={item.evaluationType || EvaluationType.THRESHOLDS}
            />
          </InlineField>
        )}
        {isEvaluationType(allEvaluation, EvaluationType.PATTERN) && (
          <>
            <InlineField
              label={t('alert.conditions.evaluation.pattern.fieldLabel')}
              labelWidth={LABEL_WIDTH}
              tooltip={t('alert.conditions.evaluation.pattern.fieldTooltip')}
              grow={true}
              disabled={!editable}
            >
              <Input
                onChange={(event) => {
                  onChange({
                    ...item,
                    evaluation: {
                      ...allEvaluation,
                      field: event.currentTarget.value,
                    },
                  });
                }}
                value={allEvaluation?.field}
                data-testid={TEST_IDS.alertConditions.fieldPatternField}
              />
            </InlineField>
            <InlineField
              label={t('alert.conditions.evaluation.pattern.patternLabel')}
              labelWidth={LABEL_WIDTH}
              required={true}
              tooltip={t('alert.conditions.evaluation.pattern.patternTooltip')}
              grow={true}
              disabled={!editable}
              invalid={!allEvaluation.pattern}
              error={t('alert.conditions.evaluation.pattern.noPatternMessage')}
            >
              <Input
                onChange={(event) => {
                  onChange({
                    ...item,
                    evaluation: {
                      ...allEvaluation,
                      pattern: event.currentTarget.value,
                    },
                  });
                }}
                value={allEvaluation.pattern}
                data-testid={TEST_IDS.alertConditions.fieldPatternPattern}
              />
            </InlineField>
          </>
        )}

        {isEvaluationType(allEvaluation, EvaluationType.THRESHOLDS) && (
          <>
            <InlineField
              label={t('alert.conditions.evaluation.thresholds.colors.label')}
              labelWidth={LABEL_WIDTH}
              shrink={true}
              grow={true}
              tooltip={t('alert.conditions.evaluation.thresholds.colors.description')}
              disabled={!editable}
              className={styles.selectField}
            >
              <Select
                options={thresholdsOptions}
                value={
                  (item.evaluation as AlertEvaluationThresholds)?.colors
                    ? (item.evaluation as AlertEvaluationThresholds).colors
                    : []
                }
                formatOptionLabel={(item) => (
                  <div className={styles.colorOption}>
                    <span
                      className={styles.colorSwatch}
                      style={{ backgroundColor: theme.visualization.getColorByName(item.value || '') }}
                    />
                    {item.label}
                  </div>
                )}
                onChange={(event: SelectableValue<string>) => {
                  const colors = Array.isArray(event) ? event.map(({ value }) => value) : [event.value];

                  /**
                   * Update colors or set empty value for global item
                   */
                  if (isGlobalAlertCondition(item) || colors.length) {
                    onChange({
                      ...item,
                      evaluation: {
                        ...((item.evaluation || { type: allEvaluation.type }) as AlertEvaluationThresholds),
                        colors,
                      },
                    });
                    return;
                  }

                  onResetValueInOverride('colors');
                }}
                isMulti={true}
                placeholder={
                  !(item.evaluation as AlertEvaluationThresholds)?.colors && !isGlobalAlertCondition(item)
                    ? thresholdsOptions
                        .filter((option) => allEvaluation.colors.includes(option.value))
                        .map((option) => option.label)
                        .join(', ') || t('alert.conditions.evaluation.thresholds.colors.placeholder')
                    : undefined
                }
                data-testid={TEST_IDS.alertConditions.fieldThresholds}
              />
            </InlineField>
          </>
        )}
        {isGlobalAlertCondition(item) && (
          <>
            <InlineField
              label={t('alert.conditions.evaluationApply.mode.label')}
              labelWidth={LABEL_WIDTH}
              shrink={true}
              grow={true}
              tooltip={t('alert.conditions.evaluationApply.mode.description')}
              disabled={!editable}
              className={styles.selectField}
              data-testid={TEST_IDS.alertConditions.fieldEvaluationApplyMode}
            >
              <RadioButtonGroup
                options={evaluationApplyModeOptions}
                value={item.evaluationApply.mode}
                onChange={(mode) => {
                  onChange({
                    ...item,
                    evaluationApply: {
                      ...item.evaluationApply,
                      mode,
                    },
                  });
                }}
              />
            </InlineField>
            {item.evaluationApply.mode === AlertEvaluationApplyMode.PERCENTAGE && (
              <InlineField
                label={t('alert.conditions.evaluationApply.percentage.label')}
                labelWidth={LABEL_WIDTH}
                shrink={true}
                grow={true}
                tooltip={t('alert.conditions.evaluationApply.percentage.description')}
                disabled={!editable}
                className={styles.selectField}
              >
                <Slider
                  min={0}
                  max={100}
                  step={1}
                  value={item.evaluationApply.percentage}
                  onChange={(percentage) => {
                    onChange({
                      ...item,
                      evaluationApply: {
                        ...item.evaluationApply,
                        percentage,
                      },
                    });
                  }}
                  marks={{
                    0: '0%',
                    25: '25%',
                    50: '50%',
                    75: '75%',
                    100: '100%',
                  }}
                  data-testid={TEST_IDS.alertConditions.fieldEvaluationApplyPercentage}
                />
              </InlineField>
            )}
          </>
        )}
      </FieldSet>
    </>
  ));
};
