import { getOptionsWithTestId } from '@bi/utils';
import {
  Alert as AlertData,
  AlertHistory,
  AlertState,
  AlertTimeRangeSource,
  DataSourceApi,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import {
  Alert,
  Button,
  CollapsableSection,
  FieldSet,
  InlineField,
  InlineFieldRow,
  Input,
  Label,
  LoadingBar,
  LoadingPlaceholder,
  RadioButtonGroup,
  Select,
  useStyles2,
} from '@grafana/ui';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { ALERT_TIME_RANGE_SOURCE_OPTIONS, ANNOTATION_OPTIONS, LABEL_WIDTH, TEST_IDS } from '../../constants';
import { useAlertConditions, usePermissions } from '../../hooks';
import { secondsToRelativeFormat } from '../../utils';
import { AlertConditions } from '../AlertConditions';
import { AlertEventDetails } from '../AlertEventDetails';
import { getStyles } from './AlertOverview.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * On Close
   */
  onClose: () => void;

  /**
   * Alert
   *
   * @type {AlertData}
   */
  alert: AlertData;

  /**
   * Loading
   *
   * @type {boolean}
   */
  loading: boolean;
}

/**
 * Alert Details
 */
export const AlertOverview: React.FC<Props> = ({ datasource, onClose, alert, loading }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Permissions
   */
  const permissions = usePermissions();

  /**
   * Dashboard Info
   */
  const { data: dashboardInfo } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        return datasource.api.grafana.getDashboardInfo(alert.dashboardId);
      },
      [alert.dashboardId]
    ),
    datasource,
  });

  /**
   * Actions
   */
  const { data: actions, loading: isActionsLoading } = useData({
    initial: [],
    query: useCallback(async (datasource) => {
      return datasource.api.actions.getAll();
    }, []),
    datasource,
  });

  /**
   * History Event
   */
  const { data: historyEvent, loading: isHistoryEventLoading } = useData<AlertHistory | null>({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (permissions.alerts.viewEventDetails) {
          return datasource.api.alerts.getLastHistoryEvent(alert.id);
        }
        return null;
      },
      [alert.id, permissions.alerts.viewEventDetails]
    ),
    datasource,
  });

  /**
   * Default Action
   */
  const { data: defaultAction, loading: isDefaultActionLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (!alert.actionIds.length) {
          return datasource.api.actions.getDefault();
        }
        return null;
      },
      [alert.actionIds.length]
    ),
    datasource,
  });

  /**
   * Panel
   */
  const panelTitle = useMemo(() => {
    if (!dashboardInfo) {
      return '';
    }

    const panel = dashboardInfo.panels.find((panel) => panel.id === alert.panelId);

    if (!panel) {
      return t('alertingPanel.panel.alertOverview.removedPanel');
    }

    return `${panel?.title || t('alertingPanel.panel.alertOverview.untitled')} [${panel?.type}]`;
  }, [alert.panelId, dashboardInfo, t]);

  /**
   * Action Options
   */
  const actionOptions = useMemo(
    () =>
      actions.map((action) => ({
        value: action.id,
        label: action.title,
      })),
    [actions]
  );

  /**
   * Annotation Options
   */
  const annotationOptions = useMemo(() => {
    return getOptionsWithTestId(ANNOTATION_OPTIONS(t), TEST_IDS.alertOverview.annotationsOption);
  }, [t]);

  /**
   * Time Range Source Options
   */
  const timeRangeSourceOptions = useMemo(() => {
    return getOptionsWithTestId(ALERT_TIME_RANGE_SOURCE_OPTIONS(t), TEST_IDS.alertOverview.timeRangeSourceOption);
  }, [t]);

  /**
   * Alert Conditions
   */
  const {
    value: conditions,
    onChange,
    initialized: conditionsAvailable,
    thresholds,
  } = useAlertConditions({ alert, dashboard: dashboardInfo });

  return (
    <div className={styles.root} data-testid={TEST_IDS.alertOverview.root}>
      {loading && (
        <div className={styles.loadingBar} data-testid={TEST_IDS.alertOverview.loadingBar}>
          <LoadingBar width={100} />
        </div>
      )}
      <InlineField
        label={t('alertingPanel.panel.alertOverview.targetLabel')}
        className={styles.target}
        labelWidth={LABEL_WIDTH}
      >
        <Label
          category={[dashboardInfo?.title ?? '']}
          className={styles.targetLabel}
          data-testid={TEST_IDS.alertOverview.target}
        >
          {panelTitle}
        </Label>
      </InlineField>

      <FieldSet label={t('alertingPanel.panel.alertOverview.timeRangeLabel')}>
        <InlineField
          label={t('alertingPanel.panel.alertOverview.timeRangeSourceLabel')}
          labelWidth={LABEL_WIDTH}
          disabled={true}
          data-testid={TEST_IDS.alertOverview.fieldTimeRangeSource}
        >
          <RadioButtonGroup options={timeRangeSourceOptions} value={alert.timeRangeSource} />
        </InlineField>

        {alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED && !!alert.timeRange && (
          <InlineFieldRow>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.from')}
              disabled={true}
              labelWidth={LABEL_WIDTH}
            >
              <Input
                value={secondsToRelativeFormat(alert.timeRange.from)}
                data-testid={TEST_IDS.alertOverview.fieldTimeRangeFrom}
              />
            </InlineField>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.to')}
              disabled={true}
              labelWidth={LABEL_WIDTH}
              grow
            >
              <Input
                value={secondsToRelativeFormat(alert.timeRange.to)}
                data-testid={TEST_IDS.alertOverview.fieldTimeRangeTo}
              />
            </InlineField>
          </InlineFieldRow>
        )}

        {alert.timeRangeSource === AlertTimeRangeSource.DASHBOARD && dashboardInfo?.time && (
          <InlineFieldRow>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.from')}
              disabled={true}
              labelWidth={LABEL_WIDTH}
            >
              <Input value={dashboardInfo?.time.from} data-testid={TEST_IDS.alertOverview.fieldTimeRangeFrom} />
            </InlineField>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.to')}
              disabled={true}
              labelWidth={LABEL_WIDTH}
              grow
            >
              <Input value={dashboardInfo?.time.to} data-testid={TEST_IDS.alertOverview.fieldTimeRangeTo} />
            </InlineField>
          </InlineFieldRow>
        )}
      </FieldSet>

      {!!conditions && (
        <AlertConditions
          value={conditionsAvailable ? conditions : []}
          onChange={onChange}
          editable={false}
          datasource={datasource}
          dashboard={dashboardInfo}
          timeRange={alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED ? alert.timeRange : undefined}
          thresholds={thresholds}
        />
      )}

      <FieldSet label={t('alertingPanel.panel.alertOverview.actionLabel')}>
        <InlineFieldRow>
          <InlineField
            label={t('alertingPanel.panel.alertOverview.actionsLabel')}
            labelWidth={LABEL_WIDTH}
            grow={true}
            disabled={true}
          >
            <Select
              onChange={() => {}}
              value={alert.actionIds.length ? alert.actionIds : defaultAction ? [defaultAction.id] : []}
              options={actionOptions}
              isMulti={true}
              isLoading={isActionsLoading || isDefaultActionLoading}
              data-testid={TEST_IDS.alertOverview.fieldActions}
            />
          </InlineField>
        </InlineFieldRow>
        <InlineFieldRow>
          <InlineField
            label={t('alertingPanel.panel.alertOverview.annotationsLabel')}
            grow={true}
            disabled={true}
            labelWidth={LABEL_WIDTH}
            data-testid={TEST_IDS.alertOverview.fieldAnnotations}
          >
            <RadioButtonGroup options={annotationOptions} value={alert.annotations} />
          </InlineField>
        </InlineFieldRow>
      </FieldSet>

      {permissions.alerts.viewEventDetails &&
        (alert.state === AlertState.ERROR || alert.state === AlertState.ALERTING) && (
          <>
            <CollapsableSection
              label={t('alertingPanel.panel.alertOverview.eventDetailsLabel')}
              isOpen={false}
              headerDataTestId={TEST_IDS.alertOverview.eventDetailsHeader}
              contentDataTestId={TEST_IDS.alertOverview.eventDetailsContent}
            >
              {isHistoryEventLoading ? (
                <Alert severity="info" title={t('basic.wait')}>
                  <LoadingPlaceholder text={t('messages.loading')} />
                </Alert>
              ) : (
                <AlertEventDetails
                  value={historyEvent?.event ? JSON.stringify(JSON.parse(historyEvent.event), null, 2) : ''}
                />
              )}
            </CollapsableSection>
            <br />
          </>
        )}

      <footer className={styles.footer}>
        <Button variant="secondary" onClick={onClose} data-testid={TEST_IDS.alertOverview.buttonClose}>
          {t('alertingPanel.panel.alertOverview.closeButton')}
        </Button>
      </footer>
    </div>
  );
};
