import { ActionIcon } from '@bi/icons';
import {
  Action,
  ActionType,
  DataSourceApi,
  notifyError,
  notifySuccess,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import { EventBus } from '@grafana/data';
import { RefreshEvent } from '@grafana/runtime';
import {
  Alert,
  Button,
  Card,
  ConfirmModal,
  Drawer,
  IconButton,
  LoadingPlaceholder,
  useStyles2,
  useTheme2,
} from '@grafana/ui';
import React, { useCallback, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { TEST_IDS } from '../../constants';
import { usePermissions } from '../../hooks';
import { ActionDetails } from '../ActionDetails';
import { TagItem, TagList, TagName } from '../TagList';
import { getStyles } from './ActionsList.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi}
   */
  datasource: DataSourceApi | null;

  /**
   * Event Bus
   *
   * @type {EventBus}
   */
  eventBus: EventBus;

  /**
   * Set Loading
   */
  setLoading: (loading: boolean) => void;
}

/**
 * Actions List
 */
export const ActionsList: React.FC<Props> = ({ datasource, eventBus, setLoading }) => {
  /**
   * Theme and Styles
   */
  const theme = useTheme2();
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Permissions
   */
  const permissions = usePermissions();

  /**
   * States
   */
  const [isAdding, setAdding] = useState(false);
  const [editAction, setEditAction] = useState<Action | null>(null);
  const [deleteAction, setDeleteAction] = useState<Action | null>(null);
  const [deleting, setDeleting] = useState(false);
  const [firstLoad, setFirstLoad] = useState(true);

  /**
   * Actions
   */
  const {
    data: actions,
    loading,
    loaded,
    update: updateActions,
    error,
  } = useData({
    datasource,
    query: useCallback((datasource) => datasource.api.actions.getAll(), []),
    initial: [],
  });

  /**
   * On Add Action
   */
  const onAddAction = useCallback(
    async ({ useFor, useForByDefault, ...action }: Action) => {
      if (!datasource || !datasource.api) {
        return;
      }

      try {
        await datasource.api.actions.create(action);
        await updateActions();

        setAdding(false);
        notifySuccess([
          t('alertingPanel.panel.actionsList.actionCreatedTitle'),
          t('alertingPanel.panel.actionsList.actionCreatedMessage', { name: action.title }),
        ]);
      } catch {}
    },
    [datasource, t, updateActions]
  );

  /**
   * On Update Action
   */
  const onUpdateAction = useCallback(
    async ({ useFor, useForByDefault, ...action }: Action) => {
      if (!datasource || !datasource.api || !editAction) {
        return;
      }

      try {
        await datasource.api.actions.update(editAction.id, action);
        await updateActions();

        setEditAction(null);
        notifySuccess([
          t('alertingPanel.panel.actionsList.actionUpdatedTitle'),
          t('alertingPanel.panel.actionsList.actionUpdatedMessage', { name: editAction.title }),
        ]);
      } catch {}
    },
    [datasource, editAction, t, updateActions]
  );

  /**
   * On Delete Action
   */
  const onDeleteAction = useCallback(
    async (action: Action) => {
      if (!datasource || !datasource.api) {
        return;
      }

      try {
        setDeleting(true);
        await datasource.api.actions.delete(action.id);
        await updateActions();

        setDeleteAction(null);
        setDeleting(false);
        notifySuccess([
          t('alertingPanel.panel.actionsList.actionDeletedTitle'),
          t('alertingPanel.panel.actionsList.actionDeletedMessage', { name: action.title }),
        ]);
      } catch {
        setDeleting(false);
      }
    },
    [datasource, t, updateActions]
  );

  /**
   * On Test Action
   */
  const onTestAction = useCallback(
    async ({ useFor, useForByDefault, ...action }: Action) => {
      if (!datasource || !datasource.api) {
        return;
      }

      const data = await datasource.api.actions.test(action);

      if (data.ok) {
        notifySuccess([
          t('alertingPanel.panel.actionsList.actionTestedTitle'),
          t('alertingPanel.panel.actionsList.actionTestedMessage', { name: action.title }),
        ]);
      } else {
        notifyError([t('api.error'), data.message]);
      }
    },
    [datasource, t]
  );

  /**
   * Refresh on dashboard update
   */
  useEffect(() => {
    /**
     * On Refresh
     */
    const subscriber = eventBus.getStream(RefreshEvent).subscribe(() => {
      updateActions();
    });

    return () => {
      subscriber.unsubscribe();
    };
  }, [eventBus, updateActions]);

  /**
   * Set Panel Loading
   */
  useEffect(() => {
    setLoading(loading);
  }, [loading, setLoading]);

  /**
   * Reset First Load
   */
  useEffect(() => {
    if (loaded) {
      setFirstLoad(false);
    }
  }, [loaded]);

  if (firstLoad && loading) {
    return (
      <div>
        <Alert severity="info" title={t('basic.wait')} data-testid={TEST_IDS.actionsList.loadingMessage}>
          <LoadingPlaceholder text={t('messages.loading')} />
        </Alert>
      </div>
    );
  }

  /**
   * Error
   */
  if (error) {
    return (
      <div>
        <Alert severity="error" title={t('messages.notAvailableTitle')} data-testid={TEST_IDS.actionsList.errorMessage}>
          {t('messages.notAvailableMessage')}
        </Alert>
      </div>
    );
  }

  return (
    <div className={styles.root} data-testid={TEST_IDS.actionsList.root}>
      {permissions.actions.add && (
        <div className={styles.header}>
          <Button icon="plus-circle" onClick={() => setAdding(true)} data-testid={TEST_IDS.actionsList.buttonAddItem}>
            {t('alertingPanel.panel.actionsList.addButton')}
          </Button>
        </div>
      )}

      {actions.length === 0 ? (
        <Alert
          title={t('alertingPanel.panel.actionsList.noItemsTitle')}
          severity="info"
          data-testid={TEST_IDS.actionsList.noItemsMessage}
        >
          {t('alertingPanel.panel.actionsList.noItemsMessage')}
        </Alert>
      ) : (
        actions.map((action: Action) => {
          const tags: TagItem[] = [
            {
              text: t(`alertingPanel.panel.actionType.${action.type}`),
              name: action.type === ActionType.REQUEST ? TagName.ACTION_REQUEST : TagName.ACTION_TEST,
            },
          ];

          if (action.isDefault) {
            tags.push({
              text: t(`alertingPanel.panel.actionsList.isDefault`),
              name: TagName.ACTION_DEFAULT,
            });
          }

          return (
            <Card
              key={action.id}
              onClick={() => {
                if (permissions.actions.edit) {
                  setEditAction(action);
                }
              }}
              data-testid={TEST_IDS.actionsList.item(action.id)}
            >
              <Card.Heading aria-label={TEST_IDS.actionsList.itemTitle}>{action.title}</Card.Heading>
              <Card.Meta>{action.type === ActionType.REQUEST && action.options?.url}</Card.Meta>
              <Card.Tags>
                <TagList tags={tags} />
              </Card.Tags>
              <Card.Figure>
                <ActionIcon size={40} fill={theme.colors.primary.main} fill2={theme.colors.border.medium} />
              </Card.Figure>
              <Card.SecondaryActions className={styles.secondaryActions}>
                {permissions.actions.test && (
                  <IconButton
                    name="heart-rate"
                    aria-label=""
                    tooltip={t('alertingPanel.panel.actionsList.testButton')}
                    onClick={() => onTestAction(action)}
                    data-testid={TEST_IDS.actionsList.buttonTestItem}
                  />
                )}
                {permissions.actions.edit && (
                  <IconButton
                    name="edit"
                    aria-label=""
                    tooltip={t('alertingPanel.panel.actionsList.editButton')}
                    onClick={() => {
                      setEditAction(action);
                    }}
                    data-testid={TEST_IDS.actionsList.buttonEditItem}
                  />
                )}
                {permissions.actions.delete && (
                  <IconButton
                    name="trash-alt"
                    aria-label=""
                    tooltip={
                      !!action.useFor?.length
                        ? t('alertingPanel.panel.actionsList.deleteButtonDisabled')
                        : t('alertingPanel.panel.actionsList.deleteButton')
                    }
                    onClick={() => setDeleteAction(action)}
                    disabled={!!action.useFor?.length}
                    data-testid={TEST_IDS.actionsList.buttonDeleteItem}
                  />
                )}
              </Card.SecondaryActions>
            </Card>
          );
        })
      )}

      {isAdding && (
        <Drawer
          title={t('alertingPanel.panel.actionsList.addDrawer.title')}
          onClose={() => setAdding(false)}
          scrollableContent
        >
          <ActionDetails datasource={datasource} onClose={() => setAdding(false)} onSave={onAddAction} />
        </Drawer>
      )}

      {!!editAction && (
        <Drawer
          title={t('alertingPanel.panel.actionsList.editDrawer.title')}
          onClose={() => setEditAction(null)}
          scrollableContent
        >
          <ActionDetails
            datasource={datasource}
            onClose={() => setEditAction(null)}
            onSave={onUpdateAction}
            initial={editAction}
          />
        </Drawer>
      )}
      {!!deleteAction && (
        <ConfirmModal
          data-testid={TEST_IDS.actionsList.confirmDeleteItemModal}
          isOpen={true}
          title={t('alertingPanel.panel.actionsList.deleteConfirmModal.title')}
          body={t('alertingPanel.panel.actionsList.deleteConfirmModal.body', { name: deleteAction.title })}
          confirmText={
            deleting
              ? t('alertingPanel.panel.actionsList.deleteConfirmModal.confirmButtonLoading')
              : t('alertingPanel.panel.actionsList.deleteConfirmModal.confirmButton')
          }
          onDismiss={() => setDeleteAction(null)}
          onConfirm={() => onDeleteAction(deleteAction)}
        />
      )}
    </div>
  );
};
