import { AlertEvaluation, AlertWithInfo, EditAlertData, EvaluationType } from '@bi/volkovlabs-engine-datasource';
import { pick } from 'lodash';
import { v4 as uuidv4 } from 'uuid';

/**
 * Convert To Edit Alert
 * Pass Particular fields to prevent type inconsistent which typescript can't verify
 */
export const convertToEditAlert = (alertWithInfo: AlertWithInfo): EditAlertData => {
  return pick(alertWithInfo, [
    'annotations',
    'dashboardId',
    'id',
    'panelId',
    'schedule',
    'status',
    'timeRange',
    'timeRangeSource',
    'title',
    'actionIds',
    'conditions',
  ]);
};

/**
 * Units
 */
const units: Record<string, number> = {
  w: 604800,
  d: 86400,
  h: 3600,
  m: 60,
  s: 1,
};

/**
 * Seconds To Relative Format
 * @param seconds
 */
export const secondsToRelativeFormat = (seconds: number): string => {
  if (seconds <= 0) {
    return 'now';
  }

  if (seconds >= units.w && seconds % units.w === 0) {
    return `now-${seconds / units.w}w`;
  }

  if (seconds >= units.d && seconds % units.d === 0) {
    return `now-${seconds / units.d}d`;
  }

  if (seconds >= units.h && seconds % units.h === 0) {
    return `now-${seconds / units.h}h`;
  }

  if (seconds >= units.m && seconds % units.m === 0) {
    return `now-${seconds / units.m}m`;
  }

  return `now-${seconds}s`;
};

/**
 * Is Evaluation Type
 */
export const isEvaluationType = <T extends EvaluationType>(
  evaluation: AlertEvaluation | Partial<AlertEvaluation>,
  evaluationType: T
): evaluation is typeof evaluation & { type: T } => {
  return evaluation.type === evaluationType;
};

/**
 * Copy Alert
 */
export const copyAlert = (alert: AlertWithInfo): AlertWithInfo => {
  return {
    ...alert,
    title: `${alert.title} (1)`,
    conditions: alert.conditions.map((condition) => ({
      ...condition,
      id: uuidv4(),
    })),
  };
};
